/* Estilo do container principal */
.send-images-container {
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding-bottom: 40px;
}

/* Estilo dos títulos */


/* Container para os uploads de imagem, colocados lado a lado */
.uploads-container {
  border-radius: 30px;
  background-color: #FAFCF9;
  width: 1200px;
  display: flex;
  justify-content: center; /* Centraliza os inputs na tela */
  gap: 30px; /* Espaço entre os inputs */
  margin-top: 20px; /* Espaço entre o título e os uploads */
  margin-bottom: 40px;
}

/* Cada componente de upload */
.image-uploader-container {
  text-align: center; /* Centraliza o conteúdo do componente de upload */
}

/* Estilo para a imagem carregada */
.image-preview img {
  max-width: 150px; /* Limita a largura da imagem */
  height: auto;
  margin-top: 10px; /* Espaço entre o texto e a imagem */
}

/* Estilo do botão de upload */
.upload-label {
  display: inline-block;
  margin-top: 10px;
}

.upload-button {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.upload-button:hover {
  background-color: #0056b3;
}

.title-create-site{
  font-size: 48px;
  font-weight: bold;
  margin-top: 20px;
}
.button-create-site {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  width: 100px;
}