    margin-top: 10px;
    border: 2px solid #ccc;
    border-radius: 10px;
    max-width: 100%;
  }

  .image-uploader-container input[type="file"] {
    display: none; /* Oculta o input de arquivo padrão */
  }
  
  .upload-button {
    display: inline-block;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    color: white;
    background-color: #007bff; /* Azul agradável */
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-top: 10px;
  }
  
  .upload-button:hover {
    background-color: #0056b3; /* Cor mais escura ao passar o mouse */
  }
  
  .upload-label {
    display: inline-block;
    cursor: pointer;
  }
  
  .upload-label:hover .upload-button {
    background-color: #0056b3; /* Aplica o efeito de hover ao rótulo */
  }

