.home-container {
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  
  .title {
    font-size: 48px;
    font-weight: bold;
    margin-top: 20px;
  }
  .title-underline {
    width: 600px; /* Largura da linha */
    border: 0;
    height: 2px;
    background: rgb(223, 239, 252); /* Cor da linha (pode ajustar para seu tom de azul) */
    margin-top: -30px;
    margin-bottom: 60px; /* Espaço abaixo da linha */
  }
  
  .content {
    border-radius: 30px;
    background-color: #FAFCF9;
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 1200px;
  }
  
  .gif-container {
    flex: 1;
    display: flex;
    justify-content: center;
  }
  
  .gif {
    max-width: 300px;
    height: auto;
  }
  .text-container {
    flex: 1;
    display: flex;
    flex-direction: column; /* Coloca os itens um abaixo do outro */
    justify-content: center;
    align-items: center;
    padding: 20px;
    font-size: 20px;
    line-height: 1.5;
  }
  
  .qrcode {
    max-width: 200px;
    height: auto; /* Adiciona um espaço entre o QR code e o texto */
  }
  