.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #3271b4; /* Azul claro/cinza para contraste */
    padding: 1rem;
  }
  .navbar-logo {
    height: 80px;
    width: auto;
    border-radius: 50px;
    
  }

  .brand-container {
    display: flex;
    align-items: center;
    text-decoration: none;
  }
  
  .brand-name {
    font-size: 1.5rem;
    font-weight: bold;
    color: #ffffff; /* Cor branca para contraste */
    padding-left: 10px;
  }

  .navbar a {
    color: #ffffff; /* Azul claro */
    text-decoration: none;
    margin-right: 1rem;
  }
  
  .navbar-links {
    list-style: none;
    display: flex;
  }
  
  .navbar-links li {
    margin-right: 1.5rem;
  }
  
  .navbar-auth .login-btn, 
  .navbar-auth .register-btn {
    padding: 0.5rem 1rem;
    border-radius: 5px;
  }
  
  .navbar-auth .login-btn {
    background-color: #3b82f6; /* Azul intermediário */
  }
  
  .navbar-auth .register-btn {
    background-color: #60a5fa; /* Azul claro */
  }
  
  .navbar-auth a:hover {
    background-color: #2563eb; /* Azul escuro para hover */
  }

